import { updateProfile } from "@firebase/auth";
import {
  collection,
  getDocs,
  orderBy,
  query,
  where,
} from "@firebase/firestore";
import { getDownloadURL, ref, uploadString } from "@firebase/storage";
import { db, auth, storageSv } from "m_base";
import { useEffect, useRef, useState } from "react";
import { v4 as uuidV4 } from "uuid";

const Profile = ({ refreshUser, user }) => {
  const [newDisplayName, setDisplayName] = useState(user.displayName);
  const [userPhoto, setUserPhoto] = useState(user.photoURL);
  //console.log(user.photoURL); í˜„ì¬ìƒíƒœí™•ì¸
  useEffect(() => {
    //ì²˜ìŒ ì‹¤í–‰
    getMyDeweets();
  });

  const getMyDeweets = async () => {
    //deweetë°›ì•„ì˜¤ê¸°
    const deweets = query(
      collection(db, "msg"),
      where("createdId", "==", user.uid),
      orderBy("createdAt") //whereì•ˆì— orderbyë¥¼ ë„£ì–´ì•¼ ì‘ë™í•¨ (ë‹¤ìˆ˜ì˜ ì¿¼ë¦¬ë¬¸ì„ ë°›ì„ìˆ˜ ì—†ìŒ)
    );
    const querySnapshot = await getDocs(deweets);
    //console.log("ìŠ¤ëƒ…ìƒ·", querySnapshot);
    querySnapshot.forEach((doc) => {
      //console.log(doc.id, " => ", doc.data());
    });
  };

  const changeProfile = async () => {
    //í”„ë¡œíŒŒì¼ í¸ì§‘
    const ok = window.confirm("Are you sure Edit Your Profile?");
    if (ok) {
      let userPhotoUrl = "";
      if (userPhoto === user.photoURL) {
        console.log("ì´ë¯¸ì§€ ì•ˆë°”ë€œ");
        userPhotoUrl = user.photoURL;
      } else if (userPhoto !== "") {
        const userPhotoRef = ref(storageSv, `${user.uid}/${uuidV4()}`);
        const response = await uploadString(
          userPhotoRef,
          userPhoto,
          "data_url"
        );
        userPhotoUrl = await getDownloadURL(response.ref);
      }
      if (newDisplayName.length > 3) {
        await updateProfile(auth.currentUser, {
          displayName: newDisplayName,
          photoURL: userPhotoUrl,
        })
          .then(() => {
            // Profile updated!
            refreshUser();
            alert("í”„ë¡œí•„ì´ ë³€ê²½ë˜ì—ˆìŠµë‹ˆë‹¤.");
            //window.location.replace("/profile");
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        alert("ê¸€ììˆ˜ê°€ ë„ˆë¬´ ì ìŠµë‹ˆë‹¤!(4ê¸€ì ì´ìƒ)");
      }
    }
  };

  const onChangeDisName = (event) => {
    //ë””ìŠ¤í”Œë ˆì´ë„¤ì„ ë°”ê¾¸ê¸°
    const {
      target: { value },
    } = event;
    setDisplayName(value);
  };

  const fileInput = useRef();
  const onClearAttachment = () => {
    setUserPhoto("");
    fileInput.current.value = "";
  };

  const onChangeProFileImg = (event) => {
    //í”„ë¡œí•„ì´ë¯¸ì§€ë°”ê¾¸ê¸°
    const {
      target: { files },
    } = event;
    const theFile = files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) => {
      //imageê°€ì ¸ì˜¨ê±° ë„£ê¸°
      const {
        currentTarget: { result },
      } = finishedEvent;
      setUserPhoto(result);
    };
    reader.readAsDataURL(theFile);
  };

  return (
    <div className="container">
      <h3>{user.displayName}ì˜ í”„ë¡œí•„</h3>
      <h1>Edit your Profile</h1>
      <form onSubmit={changeProfile} className="profileForm">
        <div>
          {" "}
          <input
            onChange={onChangeDisName}
            type="text"
            autoFocus
            placeholder="Edit Your Name"
            value={newDisplayName}
            className="formInput"
          ></input>
        </div>
        {userPhoto && (
          <div>
            <img
              src={userPhoto}
              width="100px"
              height="100px"
              alt={`${user.displayName} ì˜ í”„ë¡œí•„ì´ë¯¸ì§€`}
            />
            <button onClick={onClearAttachment}>Clear</button>
          </div>
        )}
        <div>
          {" "}
          <input
            type="file"
            accept="image/*"
            onChange={onChangeProFileImg}
            ref={fileInput}
          />
        </div>

        <input
          type="submit"
          value="Update Profile"
          className="formBtn"
          style={{
            marginTop: 10,
          }}
        />
      </form>
    </div>
  );
};
export default Profile;
