import { useEffect, useState } from "react";
import {
  collection,
  getFirestore,
  onSnapshot,
  query,
  orderBy,
} from "firebase/firestore";
import Deweet from "components/Deweet";
import HomeFactory from "components/HomeFactory";

function Home({ user, refreshUser }) { //í™ˆí™”ë©´
  const [nDeweets, setDeweets] = useState([]);
  useEffect(() => {
    // ì‹¤ì‹œê°„ìœ¼ë¡œ ë°ì´í„°ë¥¼ ë°ì´í„°ë² ì´ìŠ¤ì—ì„œ ê°€ì ¸ì˜¤ê¸°
    const q = query(collection(getFirestore(), "msg"), orderBy("createdAt"));
    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const newArray = querySnapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data(),
        };
      });
      setDeweets(newArray);
      //console.log("Current deweets in CA: ", newArray);
    });
    return () => {
      unsubscribe();
    };
  }, []);

  //í™”ë©´
  return (
    <div className="container" >
      <HomeFactory user={user} refreshUser={refreshUser} />
      <div style={{ marginTop: 30 }}>
        {nDeweets.map((deweets) => (
          <Deweet
            key={deweets.id}
            deweetObj={deweets}
            isOwned={deweets.createdId === user.uid}
            attachmentUrl={deweets.attachmentUrl}
          />
        ))}
      </div>
    </div>
  );
}
export default Home;
